.fr-measured {
  margin: 0 !important;
  min-width: 0 !important;
  min-height: 0 !important; }

.fr-box {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }

.fr-pages {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }

.fr-page {
  position: absolute;
  width: 100%;
  height: 100%; }

.fr-container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 0;
  padding: 0;
  text-align: center; }

.fr-hovering-clickable .fr-container {
  cursor: pointer; }

/*
   padding:
   youtube & vimeo always have buttons on the outside
   side button = 72 = 54 + (2 * 9 margin)
   close button = 48
*/
